import { Button } from '@material-ui/core';
import { useState } from 'react';
import Uranus from './Uranus';

const Emi = () => {
  const [select, setSelect] = useState(0);
  switch (select) {
    case 1:
      return <Uranus />;
    case 2:
      return <h1>Under Maintainance</h1>;
    default:
      return (
        <>
          <Button onClick={() => setSelect(1)} variant='outlined'>
            Uranus
          </Button>
          <Button
            style={{ marginLeft: '20px' }}
            onClick={() => setSelect(2)}
            variant='outlined'
          >
            Coming Soon
          </Button>
        </>
      );
  }
};

export default Emi;
