import { Button, Card, CardContent } from '@material-ui/core';
import { Formik } from 'formik';
import React from 'react';
import { useContext } from 'react';
import { AllData } from '../index';
import MyForm from '../Form/Form';
import { FormFieldSecond } from '../Data/Data';
import { FORM_VALIDATION_SECOND } from '../Form/FormValidation';

const SecondStep = () => {
  const { get, data, handleNext, handlePrev } = useContext(AllData);
  const handleSubmit = values => {
    values.customer_name = get.properties.customer_name;
    handleNext(values, 'post');
  };
  console.log(get);
  if (Object.keys(get).length === 0) {
    return <h4>Loading</h4>;
  } else {
    return (
      <>
        {get.response_code === 0 ? (
          <div>
            <h2>Amount : {get.amount}</h2>
            <h2>Customer Name : {get.properties.customer_name}</h2>
            <h2>
              <Card style={{ width: '500px' }}>
                <CardContent>
                  Savings :
                  {get.properties.savings.map(el => {
                    return (
                      <Card
                        key={el.account_no}
                        style={{ width: '400px', margin: '1rem' }}
                      >
                        <CardContent>
                          <h5>Account Number : {el.account_no}</h5>
                          <h5>Account Type :{el.account_type}</h5>
                          <h5>Accrued Amount : {el.accrued_amount}</h5>
                          <h5>Balance: {el.balance}</h5>
                        </CardContent>
                      </Card>
                    );
                  })}
                </CardContent>
              </Card>
            </h2>
            <Formik
              initialValues={data}
              validationSchema={FORM_VALIDATION_SECOND}
              onSubmit={handleSubmit}
            >
              <MyForm FormField={FormFieldSecond} />
            </Formik>
          </div>
        ) : (
          <>
            <h1>{get.Response}</h1>
            <Button onClick={handlePrev} variant='outlined'>
              Back
            </Button>
          </>
        )}
      </>
    );
  }
};

export default SecondStep;
