import { Button } from '@material-ui/core';
import { useState } from 'react';
import EMI from './EMI/Emi';
import Remittance from './REMITTANCE/Remittance';
const App = () => {
  const [select, setSelect] = useState(0);
  switch (select) {
    case 1:
      return <Remittance />;
    case 2:
      return (
        <>
          <Button variant='outlined' onClick={() => setSelect(1)}>
            Remittance
          </Button>
          <Button
            style={{ marginLeft: '20px' }}
            variant='outlined'
            onClick={() => setSelect(2)}
          >
            EMI
          </Button>
        </>
      );
    default:
      return <EMI />;
  }
};

export default App;
