import FirstStep from './Steps/FirstStep';
import FinalStep from './Steps/FinalStep';
import SecondStep from './Steps/SecondStep';
import { useState } from 'react';
import Api from './Data/Api';

import { createContext } from 'react';

const AllData = createContext();

const Uranus = () => {
  const [step, setStep] = useState(0);
  const [res, setRes] = useState({});
  const [get, setGet] = useState({});
  const [data, setData] = useState({
    agent: '',
    request_id: '',
    customer_name: '',
    mobile_number: '',
    amount: 0,
    purpose: '',
  });
  const submitData = values => {
    const payload = {
      request_id: values.request_id,
      amount: values.amount,
      mobile_number: values.mobile_number,
      customer_name: values.customer_name,
    };

    Api.post('/payment/', payload)
      .then(res => setRes(res.data))
      .catch(err => err.message);
    console.log('Form Submitted', values);
  };
  const getData = values => {
    const payload = {
      request_id: values.request_id,
    };
    console.log(values.request_id);
    Api.post('/get_detail/', payload)
      .then(res => {
        setGet(res.data);
      })
      .catch(err => err.message);
    console.log('Get Submitted', values);
  };
  const handleNext = (values, flag) => {
    if (flag === 'post') {
      submitData(values);
    }
    if (flag === 'get') {
      getData(values);
    }
    setStep(prev => prev + 1);
    setData(prev => ({ ...prev, ...values }));
  };
  const handlePrev = () => {
    setRes({});
    setGet({});
    setStep(prev => prev - 1);
  };

  const Steps = [<FirstStep />, <SecondStep />, <FinalStep />];

  return (
    <>
      <AllData.Provider value={{ handleNext, data, get, res, handlePrev }}>
        {Steps[step]}
      </AllData.Provider>
    </>
  );
};

export default Uranus;
export { AllData };
