import * as Yup from 'yup';
const required = Yup.string().required();
export const FORM_VALIDATION_FIRST = Yup.object().shape({
  request_id: required,
});
export const FORM_VALIDATION_SECOND = Yup.object().shape({
  mobile_number: required,
  amount: Yup.number().required().min(100),
  purpose: required,
});
