import TextWrapper from '../../../../utility/TextWrapper';
import Select from '../../../../utility/Select';
import { Button, Grid } from '@material-ui/core';
import { Form } from 'formik';

const MyForm = ({FormField}) => {
  return (
    <>
      <Form>
        <Grid direction='column' container spacing={2}>
          {FormField.map(el => {
            return (
              <Grid xs={6} key={el.name} item>
                {el.options ? (
                  <Select
                    name={el.name}
                    label={el.label}
                    options={el.options}
                  />
                ) : (
                  <TextWrapper name={el.name} label={el.label} />
                )}
              </Grid>
            );
          })}
          <Grid item>
            <Button type='submit' variant='outlined'>
              Next
            </Button>
          </Grid>
        </Grid>
      </Form>
    </>
  );
};

export default MyForm;
