import { Formik } from 'formik';
import React, { useContext } from 'react';
import { AllData } from '../index';
import MyForm from '../Form/Form';
import { FORM_VALIDATION_FIRST } from '../Form/FormValidation';
import { FormFieldFirst } from '../Data/Data';
const FirstStep = () => {
  const { data, handleNext } = useContext(AllData);
  const handleSubmit = values => {
    handleNext(values, 'get');
  };
  return (
    <>
      <Formik
        initialValues={{ ...data }}
        validationSchema={FORM_VALIDATION_FIRST}
        onSubmit={handleSubmit}
      >
        <MyForm FormField={FormFieldFirst} />
      </Formik>
    </>
  );
};

export default FirstStep;
